.container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 20px;
    height: 100vh;
    margin: 0 auto;
  
}

.loading-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5); 
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999; 
    backdrop-filter: blur(3px);
}

/* Spinner style from css loaders https://cssloaders.github.io/ */

.spinner {
    width: 48px;
    height: 48px;
    border: 5px solid #FFF;
    border-bottom-color: transparent;
    border-radius: 50%;
    display: inline-block;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
}

@keyframes rotation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
} 

/* Spinner style from css loaders https://cssloaders.github.io/ */

.error-message-container {
    background-color: white;
    border: 5px red solid;
}

.error-message {
    font-size: 1.2rem;
    font-weight: bold;
    padding: 10px;
    color: black;
    text-align: center;
}

.top-container {
    margin-top: 6rem;
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
}

.top-container div {
    display: flex;
    gap: 10px;
}


.material-icons, .loc h2 {
	font-size: 2.2rem;
}

.material-icons {
    margin-top: 0.2rem;
}

.unit-btn {
    font-size: 1.6rem;
	background-color: #000009; 
	color: #FFFFFF;
	padding: 0.5rem;
	text-align: center;
	cursor: pointer;
	border-radius: 50%;
    opacity: 0.5;
}

.unit-btn.active {
    opacity: 1;
}

@media (max-width: 768px) {
    .material-icons, .loc h2 {
        font-size: 1.8rem;
    }
    
    .unit-btn {
        font-size: 1.2rem;
    }
    

}

@media (max-width: 510px) { 
    .material-icons, .loc h2 {
        font-size: 1.5rem;
    }
    
    
    .unit-btn {
        font-size: 1rem;
    }
}

@media (max-width: 510px) and (max-height: 932px) {
    
    .top-container {
        margin-top: 7rem;
    }
}

