.nav {
	padding: 1rem;
	position: fixed;
	background: #000009;
	width: 100vw;
	height: 80px;
	z-index: 10;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.nav-item {
	width: 30%;
}

.app-name {
	color: #FFFAFA;
	font-size: 1.8rem;
	font-weight: bold;
	cursor: pointer;
}

.search-input-container {
	width: 40%;
	margin: 0 auto;
   
}

.search-input {
	border-radius: 7px;
	padding: 0.5rem;
	font-size: 1.2rem;
	background-color: #FFFAFA;
	margin: 0 auto;
}

@media (max-width: 768px) {
    .nav {
		padding: 0.5rem;
		height: 90px;
		z-index: 10;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		gap: 5px;
	}

	.nav-item {
		display: none;
	}

	.nav-item-app-name {
		display: flex;
		width: 100%;
	}
	
	.app-name {
		font-size: 1.2rem;
	}
	
	.search-input-container {
		width: 100%;
		margin: 0 auto;
	   
	}
	
	.search-input {
		border-radius: 7px;
		padding: 0.5rem;
		font-size: 1rem;
	}
}